<template>
  <div style="background: #eee; padding: 20px">
    <div v-for="project in projects" :key="project.projectId">
      <Card :bordered="false" class="card">
        <h1 slot="title">{{ project.projectTitle }}</h1>
        <p>{{ project.projectDescription }}</p>
        <p>åä½äººå¡: {{ project.teamMember }}</p>
        <p class="project-progress">å®æç</p>

        <div>
          <Progress
            :percent="project.progressPercent"
            v-model="project.progressPercent"
          ></Progress>
          <Button-group size="large">
            <Button icon="ios-plus-empty" @click="add(project.projectId)"
              ><p>+</p></Button
            >
            <Button icon="ios-minus-empty" @click="minus(project.projectId)"
              ><p>-</p></Button
            >
          </Button-group>
        </div>
      </Card>
    </div>
  </div>
</template>
<script>

export default {
  mounted() {
    this.fetchProjectsData()
  },
  data() {
    return {
      projects: [
        {
          projectId: 1,
          projectTitle: 'ï¼¡ååå¸å ´èª¿æ¥å ±å',
          projectDescription: '2021å¹´ç¬¬ä¸å­£é·å®é¡ï¼ç«¶æ¥­åæï¼å»£åé ç®è½åææ¬......ç­ã',
          progressPercent: 0,
          teamMember: ['çå°æ', 'æå°ç±'],
        },
        {
          projectId: 2,
          projectTitle: 'Bååéç¼é²åº¦',
          projectDescription: 'æ°åç ç¼',
          progressPercent: 40,
          teamMember: ['è¡å°ç¾', 'çå°æ¹'],
        }
      ]
    }
  },
  methods: {
    fetchProjectsData() {

      const projectList = JSON.parse(localStorage.getItem('projectList')) || []
      if (projectList.length === 0) {
        localStorage.setItem('projectList', JSON.stringify(this.projects))
      }
      this.projects = [...projectList]
    },
    add(projectId) {
      const newList = []
      this.projects.forEach((proj) => {
        if (proj.projectId === projectId) {

          if (proj.progressPercent >= 100) {
            return
          }

          const newprogressPercent = proj.progressPercent += 10
          const data = {
            ...proj,
            progressPercent: newprogressPercent
          }

          newList.push(data)
        } else {
          return newList.push(proj)
        }
      })
      localStorage.setItem('projectList', JSON.stringify(newList))

    },
    minus(projectId) {
      // console.log('minus', projectId)
      const newList = []
      this.projects.forEach((proj) => {
        if (proj.projectId === projectId) {

          if (proj.progressPercent <= 0) {
            return
          }

          const newprogressPercent = proj.progressPercent -= 10;
          const data = {
            ...proj,
            progressPercent: newprogressPercent
          }

          newList.push(data)

        } else {
          return newList.push(proj)
        }
      })
      localStorage.setItem('projectList', JSON.stringify(newList))

    }
  }
}
</script>

